<template>
    <div class="header">
        <div class="background">
            <img :src="seller.avatar">
        </div>
        <div  class="header-top">
            <img :src="seller.avatar" alt="" class="avatar">
            <div class="right">
                <div>
                    <img src="../../../static/imgs/brand.png" alt="" class="brand">
                    <p class="title">{{seller.name}}</p>
                </div>
                <div>
                    <p class="txt1">{{seller.description}}/{{seller.deliveryTime}}分钟送达</p>
                </div>
                <div>
                    <img src="../../../static/imgs/decrease.png" alt="" class="decrease">
                    <p class="txt2" v-if='seller.supports'>{{seller.supports[0].description}}</p>
                    <p class="txt3" v-if='seller.supports' @click="show">{{seller.supports.length}}个&nbsp&nbsp></p>
                </div>
            </div>
        </div> 
        <div class="header-bottom" @click="show">
            <img src="../../../static/imgs/bulletin.png" alt="" class="bulletin">
            <p class="txt4">{{seller.bulletin}}</p>
            <p class="txt5"> > </p>
        </div>
        <transition enter-active-class='animated zoomIn' leave-active-class='animated zoomOut'>
            <Detail v-if="showdetail"></Detail>
        </transition>
    </div>
</template>

<script>
    import {mapGetters} from 'vuex'
    import Detail from '../detail/detail.vue'

    export default {
        computed:{
            ...mapGetters([
                'seller',
                'showdetail'
            ])
        },
        components:{
            Detail
        },
        created(){
            this.$store.dispatch('getSeller');
        },
        methods:{
            show(){
                this.$store.dispatch('showDetail');
            }
        }
    }
</script>

<style lang="less" scoped>
    .header{
        width:100%;
        height:2.68rem;
        background-color:rgba(7,17,27,0.5);
        position:relative;
        overflow:hidden;
        // 背景图
        .background{
            z-index: -5;
            filter:blur(10px);
            position:absolute;
            top:0;
            left:0;
            right:0;
            bottom:0;
            img{
                width:100%;
                height:100%;
            }
        }
        .header-top{
            padding-top:0.48rem;
            padding-bottom:0.36rem;
            padding-left:0.48rem;
            // 图片
            .avatar{
                width:1.28rem;
                height:1.28rem;
                border-radius:0.04rem;
                z-index:999;
            }
            // 右边文字
            .right{
                float:left;
                position:absolute;
                left:2.08rem;
                top:0.52rem;
                // 品牌标志
                .brand{
                    width:0.58rem;
                }
                // 品牌名
                .title{
                    float:right;
                    position:relative;
                    left:0.12rem;
                    font-size: 0.32rem;
                    color: rgb(255,255,255);
                    font-weight: bold;
                    line-height: 0.36rem;
                }
                // 文字
                .txt1{
                    position:relative;
                    top:0.16rem;
                    font-size:0.24rem;
                    color: rgb(255,255,255);
                    font-weight: 200;
                    line-height: 0.24rem;
                }
                // 减免标志
                .decrease{
                    width:0.32rem;
                    position:relative;
                    top:0.28rem;
                }
                // 减免信息
                .txt2{
                    position:relative;
                    left:0.40rem;
                    font-size:0.20rem;
                    color: rgb(255,255,255);
                    font-weight: 200;
                    line-height: 0.24rem;
                }
                // 5个信息图标
                .txt3{
                    background-color:rgba(0,0,0,0.2);
                    border-radius:0.24rem;
                    padding:0.14rem 0.16rem;
                    float:right;
                    position:relative;
                    top:-0.4rem;
                    left:1.60rem;
                    font-size:0.20rem;
                    color: rgb(255,255,255);
                    font-weight: 200;
                    line-height: 0.24rem;
                }
            }
        }
        // 公告
        .header-bottom{
            width:100%;
            background-color:rgba(0,0,0,0.2);
            height:0.56rem;
            position:relative;
            bottom:0rem;
            // 公告标志
            .bulletin{
                width:0.50rem;
                position:relative;
                left:0.24rem;
                top:0.14rem;
            }
            // 公告文字
            .txt4{
                position:absolute;
                left:0.84rem;
                top: -0.015rem;
                right:0.32rem;
                overflow:hidden;
                white-space:nowrap;
                text-overflow:ellipsis;
                font-size:0.20rem;
                color: rgb(255,255,255);
                font-weight: 200;
                line-height: 0.56rem;
            }
            // 显示箭头
            .txt5{
                position:absolute;
                right:0.14rem;
                top: -0.02rem;
                font-size:0.26rem;
                color: rgb(255,255,255);
                font-weight: 200;
                line-height: 0.56rem;
            }
        }
    }
    
</style>


