<template>
    <div class="fooddetail" v-show='showFoodDetail'>
        <div class="foodWrapper" ref="foodDetail">
            <div>
                <div class="detail">
                    <img :src="value.image" alt="">
                    <div class="returnPage" @click="closeFooddetail">
                        <i class="icon iconfont">&#xe612;</i>
                    </div>
                    <div class="cont1">
                        <p class="text1">{{value.name}}</p>
                        <p class="text2">{{value.description}}</p>
                        <p class="text3">月售{{value.sellCount}}份&nbsp;&nbsp;好评率{{value.rating}}%</p>
                        <div class="priceCount">
                            <p class="text4">
                                ￥{{value.price}}&nbsp;&nbsp;
                                <s class="text5" v-if="value.oldPrice > 0">￥{{value.oldPrice}}</s>
                            </p>
                            <div class="shoppingCart">
                                <showcart class="cart" v-if="value.count" :food="value"></showcart>
                                <button v-if="value.count == 0 ||!value.count" @click.stop="showCount"><span>加入购物车</span></button>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="empty1"></div>
                <div class="description">
                    <p class="text6">商品介绍</p>
                    <p class="text7">{{value.info}}</p>
                </div>
                <div class="empty2"></div>
                <div class="rating">
                    <p class="text8">商品评价</p>
                    <div class="ratingCount">
                        <button class="all" @click="getAll"><span>全部{{value.ratings.length}}</span></button>
                        <button class="satisfy" @click="getSatisfy"><span>满意{{num}}</span></button>
                        <button class="unsatisfy" @click="getUnsatisfy"><span>不满意{{value.ratings.length-num}}</span></button>
                    </div>
                    <div class="show" @click="status=!status">
                        <i class="icon iconfont" :class="status?'on':'off'">&#xe608;</i>
                        <span>只看有内容的评价</span>
                    </div>
                    <div class="cont">
                        <ul>
                            <li v-for="(v,k) in value.ratings" :key='k' v-show="all || yes === v.rateType" v-if="v.text||status">
                                <div class="contTop">
                                    <div class="contLeft">
                                        <p>{{rateTime[k]}}</p> 
                                    </div>
                                    <div class="contRight">
                                        <span class="name">{{v.username}}</span>
                                        <img :src="v.avatar" alt="">
                                    </div>
                                </div>
                                <div class="contBottom">
                                    <div class="cont2">
                                        <i v-if="v.rateType == 0" class="iconfont icon-damuzhion"></i>
                                        <i v-else="v.rateType == 1" class="iconfont icon-down"></i>
                                    </div>
                                    <p class="text9">{{v.text}}</p>
                                </div>
                            </li>
                        </ul>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import Vue from 'vue'
    import {mapGetters} from 'vuex'
    import BScroll from 'better-scroll'
    import account from '../detail/account.vue'
    import showcart from '../detail/shoppingCart.vue'

    export default {
        data(){
            return {
                all: true,
                yes: -1 ,
                status:true,
            }
        },
        components:{
            showcart
        },
        computed:{
            ...mapGetters([
                'showFoodDetail'
            ]),
            rateTime(){
                var rateTime_arr=[];
                for(var i in this.value.ratings){
                    var rateTime=this.value.ratings[i].rateTime;
                    var time= new Date(rateTime);
                    var y = time.getFullYear();//年
                    var m = time.getMonth() + 1;//月
                    var d = time.getDate();//日
                    var h = time.getHours();//时
                    var mm = time.getMinutes();//分
                    var s = time.getSeconds();//秒
                    rateTime_arr.push(y+"-"+m+"-"+d+" "+h+":"+mm+":"+s)
                }
                // console.log(rateTime_arr);
                return rateTime_arr
            },
            num(){
                return this.value.ratings.filter((item) => {
                    return item.rateType === 0
                }).length
            }
        },
        created(){
            this.$nextTick( () => {
                setTimeout( ()=> {
                    if( !this.foodDetailBS ){
                        this.foodDetailBS = new BScroll (this.$refs.foodDetail,{
                            click : true
                        });
                    }else{
                        this.foodDetailBS.refresh();
                    }
                },0)
            });
        },
        methods:{
            getSatisfy() {
                this.yes = 0
                this.all = false
            },
            getUnsatisfy() {
                this.yes = 1
                this.all = false
            },
            getAll() {
                this.yes = -1
                this.all = true
            },
            closeFooddetail(){
                this.$store.dispatch('closeFoodDetail')
            },
            showCount(){
                Vue.set(this.value,'count',1);
            }
        },
        props:["value"]
    }
</script>

<style lang="less" scoped>
    .fooddetail{
        position:fixed;
        top:0;
        left:0;
        width:100%;
        height:100%;
        z-index: 99;
        background:white;
        overflow:hidden;
        // 主体内容
        .foodWrapper{
            height:7rem;
            // 食物详情
            .detail{
                border-bottom:1.5px solid rgba(7,17,27,0.1);
                & > img {
                    height:7.50rem;
                    width:100%;
                }
                .returnPage{
                    position:relative;
                    width:0.6rem;
                    top: -7.20rem;
                    left:0.36rem;
                    .iconfont{
                        color:white;
                        font-size:0.6rem;
                    }
                }
                .cont1{
                    margin: -0.36rem 0.36rem 0.36rem 0.36rem;
                    .text1{
                        margin-top:0.16rem;
                        font-size:0.28rem;
                        font-weight:700;
                        color:rgb(7,17,27);
                        line-height:0.28rem;
                    }
                    .text2{
                        margin-top:0.18rem;
                        font-size:0.20rem;
                        color:rgb(147,153,159);
                        line-height:0.20rem;
                    }
                    .text3{
                        margin-top:0.18rem;
                        font-size:0.20rem;
                        color:rgb(147,153,159);
                        line-height:0.20rem;
                    }
                    .priceCount{
                        display:flex;
                        margin:0.36rem 0 0.36rem 0;
                        .text4{
                            font-size:0.28rem;
                            font-weight:700;
                            color:rgb(240,20,20);
                            line-height:0.48rem;
                        }
                        .text5{
                            font-size:0.20rem;
                            font-weight:700;
                            color:rgb(147,153,159);
                            line-height:0.48rem;
                        }
                        .shoppingCart{
                            margin-left:4.2rem;
                            & > button {
                                border:0;
                                width:1.48rem;
                                height:0.48rem;
                                border-radius:0.24rem;
                                background-color:rgb(0,160,220);
                                span {
                                    font-size:0.20rem;
                                    color:rgb(255,255,255);
                                    line-height:0.24rem;
                                }
                            }
                        }
                    }
                }
            }
            // 间隔
            .empty1{
                width:100%;
                height:0.36rem;
                background:#f3f5f7;
            }
            // 食物介绍
            .description{
                border-top:1.5px solid rgba(7,17,27,0.1);
                border-bottom:1.5px solid rgba(7,17,27,0.1);
                .text6{
                    margin:0.36rem 0.36rem 0.12rem 0.36rem;
                    font-size:0.28rem;
                    font-weight:700;
                    color:rgb(7,17,27);
                    line-height:0.28rem;
                }
                .text7{
                    margin:0 0.52rem 0.36rem 0.52rem;
                    font-size:0.24rem;
                    font-weight:200;
                    color:rgb(77,85,93);
                    line-height:0.48rem;
                }
            }
            // 间隔
            .empty2{
                width:100%;
                height:0.36rem;
                background:#f3f5f7;
            }
            // 评价
            .rating{
                border-top:1.5px solid rgba(7,17,27,0.1);
                .text8{
                    margin:0.36rem 0.36rem 0.12rem 0.36rem;
                    font-size:0.28rem;
                    font-weight:700;
                    color:rgb(7,17,27);
                    line-height:0.28rem;
                }
                // 按钮
                .ratingCount{
                    border-bottom:1px solid rgba(7,17,27,0.1);
                    margin:0.36rem;
                    & > button {
                        padding:0.16rem 0.24rem;
                        margin:0 0.16rem 0.36rem 0;
                        border:0;
                    }
                    // 全部
                    .all{
                        border-radius:0.02rem;
                        background-color:rgb(0,160,220);
                        & > span {
                            font-size:0.24rem;
                            color:rgb(255,255,255);
                            line-height:0.32rem;
                        }
                    }
                    // 满意
                    .satisfy{
                        border-radius:0.02rem;
                        background-color:skyblue;
                        & > span {
                            font-size:0.24rem;
                            color:rgb(77,85,93);
                            line-height:0.32rem;
                        }
                    }
                    // 不满意
                    .unsatisfy{
                        border-radius:0.02rem;
                        background-color:rgba(7,17,27,0.1);
                        & > span {
                            font-size:0.24rem;
                            color:rgb(77,85,93);
                            line-height:0.32rem;
                        }
                    }
                }
                // 对勾图标
                .show{
                    padding-bottom:0.24rem;
                    border-bottom:1.5px solid rgba(7,17,27,0.1);
                    .on{
                        font-size:0.48rem;
                        color:rgb(147,153,159);
                        line-height:0.48rem;
                        margin:0.36rem;
                    }
                    .off{
                        font-size:0.48rem;
                        color:rgb(0, 160, 220);
                        line-height:0.48rem;
                        margin:0.36rem;
                    }
                    & > span {
                        font-size:0.24rem;
                        color:rgb(147,153,159);
                        line-height:0.48rem;
                    }
                }
                // 评价展示
                .cont{
                    li{
                        margin:0.32rem;
                        border-bottom:1.5px solid rgba(7,17,27,0.1);
                        .contTop{
                            display:flex;
                            justify-content:space-between;
                            // 日期时间
                            .contLeft{
                                & > p {
                                    font-size:0.20rem;
                                    color:rgb(147,153,159);
                                    line-height:0.24rem;
                                }
                            }
                            // 称呼 头像
                            .contRight{
                                .name{
                                    font-size:0.20rem;
                                    color:rgb(147,153,159);
                                    line-height:0.48rem;
                                    padding-right:0.12rem;
                                }
                                & > img {
                                    border-radius:50%;
                                    width:0.32rem;
                                }
                            }
                        }
                        // 点赞 倒彩 评价文本
                        .contBottom{
                            display:flex;
                            margin-bottom:0.32rem;
                            .cont2{
                                // 点赞
                                .icon-damuzhion{
                                    color: rgb(0, 160, 220);
                                    font-size: 0.24rem;
                                    line-height: 0.48rem;
                                }
                                //  倒彩
                                .icon-down{
                                    color: rgb(147, 153, 159);
                                    font-size: 0.24rem;
                                    line-height: 0.48rem;
                                }
                            }
                            .text9{
                                padding:0.08rem 0 0 0.08rem;
                                color: rgb(7,17,27);
                                font-size: 0.24rem;
                                line-height: 0.32rem;
                            }
                        }
                    }
                }
            }
        } 
    }
</style>


