import type from '../types'
import axios from 'axios'

const state = {
    goods: [],
    showFoodDetail: false,
    showFoodList: 0,
}

const getters = {
    goods(state) {
        return state.goods
    },
    showFoodDetail(state) {
        return state.showFoodDetail;
    },
    showFoodList(state) {
        return state.showFoodList;
    }
}

const actions = {
    getgoods({ commit, state }) {
        axios.get('../../../static/data.json')
            .then(resp => {
                // console.log(resp);
                if (resp.data.errno === 0) {
                    commit(type.GET_GOODS, resp.data.goods);
                }
            })
    },
    showFoodDetail({ commit }) {
        commit(type.SHOW_FOODDETAIL);
    },
    closeFoodDetail({ commit }) {
        commit(type.HIDE_FOODDETAIL);
    },
    showFoodList({ commit }, f) {
        commit(type.SHOW_FOODLIST, f)
    }
    
}

const mutations = {
    [type.GET_GOODS](state, data) {
        state.goods = data;
    },
    [type.SHOW_FOODDETAIL](state) {
        state.showFoodDetail = true;
    },
    [type.HIDE_FOODDETAIL](state) {
        state.showFoodDetail = false;
    },
    [type.SHOW_FOODLIST](state, f) {
        state.showFoodList = f;
    }
}

export default {
    state,
    getters,
    actions,
    mutations
}


