<template>
    <div class="account">
        <div class="content">
            <div class="left">
                <div class="cart-wrap">
                    <div class="round" @click = "showList" :class="totalCount?'on':'off'">
                        <i class="icon iconfont">&#xf0082;</i>
                    </div>
                    <div class="num" v-if="totalCount">{{totalCount}}</div>
                </div>
                <div class="price">￥{{totalPrice}}</div>
                <div class="desc">另需配送费￥{{seller.deliveryPrice}}元</div>
            </div>
            <div class="right"  @click="pay">
                <div class="pay" :class='{"enough":totalPrice >= seller.minPrice}'>
                    {{payDesc}}
                </div>
            </div>
        </div>
        <v-list :food="selectFoods" v-if="showflag"></v-list>
    </div>
</template>

<script>
    import {mapGetters} from 'vuex'
    import foodlist from '../detail/foodList.vue'

    export default {
        components : {
            'v-list':foodlist
        },
        computed:{
            ...mapGetters([
                'seller',
                'goods',
                'showAccount',
                'showFoodList'
            ]),
            selectFoods(){
                let foods = [];
                this.goods.forEach( good => {
                    good.foods.forEach( food => {
                        if (food.count > 0){
                            foods.push(food);
                        }
                    })
                })
                return foods;
            },
            totalPrice(){
                let total = 0;
                this.selectFoods.forEach( food => {
                    total += food.price * food.count;
                });
                return total;
            },
            totalCount(){
                let count = 0;
                this.selectFoods.forEach( food => {
                    count += food.count;
                });
                return count;
            },
            showflag() {
                return this.totalCount>0 && this.showFoodList
            },
            payDesc(){
                if( this.totalPrice === 0 ){
                    return `￥${this.seller.minPrice}元起送`
                }else if( this.totalPrice < this.seller.minPrice ){
                    return `还差${this.seller.minPrice - this.totalPrice}元起送`
                }else{
                    return `去结算`
                }
            }
        },
        created(){
            this.flagcount = 1
        },
        methods:{
            showList(){
                if(this.totalCount) {
                this.$store.dispatch('showFoodList',this.flagcount++%2)
                }else{
                    this.$store.dispatch('showFoodList',false)
                }
            },
            pay(){
                alert(this.totalPrice+this.seller.deliveryPrice);
            }
        }
    }
</script>

<style lang="less" scoped>
    .account{
        height:0.92rem;
        width: 100%;
        position: fixed;
        bottom: 0;
        left: 0;
        z-index: 50;
        .content{
            display:flex;
            background: #141d27;
            // 左边
            .left{
                flex:1;
                .cart-wrap{
                    // 大圆
                    width:1.16rem;
                    height:1.16rem;
                    display:inline-block;
                    vertical-align: top;
                    position: relative;
                    bottom:0.2rem;
                    margin-left: 0.24rem;
                    margin-bottom: 0.04rem;
                    padding:0.14rem;
                    background: #141d27;
                    border-radius: 50%;
                    box-sizing: border-box;
                    // 小圆
                  
                    .off{
                        width: 0.88rem;
                        height: 0.88rem;
                        border-radius:50%;
                        line-height: 0.88rem;
                        text-align: center;
                        background-color: #2b343c;
                        &.highlight{
                            background:#00a0dc;
                        }
                        // 购物车
                        i{
                            font-size: 0.46rem;
                            color:rgba(255,255,255,0.4);
                            &.highlight{
                                color: white;
                            }
                        }
                    }
                    .on{
                        width: 0.88rem;
                        height: 0.88rem;
                        border-radius:50%;
                        line-height: 0.88rem;
                        text-align: center;
                        background-color:skyblue;
                        &.highlight{
                            background:#00a0dc;
                        }
                        // 购物车
                        i{
                            font-size: 0.46rem;
                            color:rgba(255,255,255,0.4);
                            &.highlight{
                                color: white;
                            }
                        }
                    }
                    // 数量
                    .num{
                        position: absolute;
                        top:0;
                        right: 0;
                        width: 0.48rem;
                        height: 0.34rem;
                        line-height: 0.38rem;
                        text-align: center;
                        border-radius: 0.16rem;
                        font-size: 0.09rem;
                        font-weight: 700;
                        color:white;
                        background:rgb(240,20,20);
                        box-shadow:0 4px 8px 0 rgba(0,0,0,0.4);
                    }
                }
                // 价格
                .price{
                    font-size:0.32rem;
                    font-weight: 700;
                    line-height: 0.48rem;
                    color:rgba(255,255,255,0.4);
                    position:relative;
                    bottom:1rem;
                    left:1.56rem;
                }
                // 配送费
                .desc{
                    border-left:1px solid rgba(255,255,255,0.1);
                    font-size:0.32rem;
                    line-height: 0.48rem;
                    color:rgba(255,255,255,0.4);
                    position:relative;
                    bottom:1.48rem;
                    left:2.34rem;
                    padding:0 0.24rem;
                }
            }
            // 起送价
            .right{
                width:2.10rem;
                background:#2b343c;
                z-index:999;
                .pay{
                    font-size:0.24rem;
                    line-height: 0.48rem;
                    color:rgba(255,255,255,0.4);
                    font-weight: 700;
                    text-align:center;
                    padding-top:0.2rem;
                }
            }
        }
    }
</style>


