<template>
    <div class="cart">
        <transition name="slide-fade">
            <div class="reduce" v-show="food.count" @click.stop ='decrement'>
                <i class="icon iconfont" v-if="show">&#xe631;</i>
            </div>
        </transition>
        <div class="num" v-show="food.count">
            <span v-if="show">{{food.count}}</span>
        </div>
        <div class="plus" @click.stop = 'increment'>
            <i class="icon iconfont">&#xe611;</i>
        </div>
    </div>
</template>

<script>
    import Vue from 'vue'
    import {mapGetters} from 'vuex'
    
    export default {
        data(){
            return {
                show:true
            }
        },
        props:['food'],
        computed:{
            ...mapGetters([
                'showCart'
            ])
        },
        methods:{
            increment(){
                if( !this.food.count ){
                    Vue.set(this.food,'count',1);
                }else{
                    this.food.count ++;
                }
            },
            decrement(){
                if( this.food.count ){
                    this.food.count --;
                }
                setTimeout(()=>{
                    this.$emit('decreament')
                },20)
            }
        }
    }
</script>

<style lang="less" scoped>
    .slide-fade-enter-active {
        transition: all .3s ease;
    }
    .slide-fade-leave-active {
        transition: all .8s cubic-bezier(1.0, 0.5, 0.8, 1.0);
    }
    .slide-fade-enter, .slide-fade-leave-to
        /* .slide-fade-leave-active for below version 2.1.8 */ {
        transform: translateX(10px);
        opacity: 0;
    }
    .cart{
        display:flex;
        .reduce{
            font-size:0.20rem;
            color:rgb(0,160,220);
            line-height:0.48rem;
        }
        .num{
            & > span {
                padding:0 0.18rem;
                font-size:0.20rem;
                color:rgb(147,153,159);
                line-height:0.48rem;
            }
        }
        .plus{
            font-size:0.20rem;
            color:rgb(0,160,220);
            line-height:0.48rem;
        }
    }
</style>


