<template>
    <div class="seller" ref="sellers">
        <div>
            <div class="seller-top">
                <div class="top-brand">
                    <div class="left">
                        <p class="brand">{{seller.name}}</p>
                        <div class="left-cont">
                            <Star class="star"></Star>
                            <p class="top-text1">(661)</p>
                            <p class="top-text2">月售{{seller.sellCount}}单</p>
                        </div>
                    </div>
                    <div class="collect">
                        <i class="icon iconfont" :class="{'like':status,'unlike':!status}" @click="changeStatus($event)">&#xe64a;</i>
                        <p class="collect-text">{{status? "已收藏" : "收藏"}}</p>
                    </div>
                </div>
                <div class="h"></div>
                <div class="top-detail">
                    <div class="tab-item">
                        <p class="text1">起送价</p>
                        <p class="text2">{{seller.minPrice}}元</p>
                    </div>
                    <div class="tab-item">
                        <p class="text1">商家配送</p>
                        <p class="text2">{{seller.deliveryPrice}}元</p>
                    </div>
                    <div class="tab-item" style="border-right:none;">
                        <p class="text1">平均配送时间</p>
                        <p class="text2">{{seller.deliveryTime}}分钟</p>
                    </div>
                </div>
            </div>
            <div class="empty1"></div>
            <div class="seller-active">
                <p class="active-title">公告与活动</p>
                <p class="bulletin">{{seller.bulletin}}</p>
                <div class="active-info">
                    <div v-for="(v,k) in seller.supports" class="info">
                        <img src="../../../static/imgs/decrease.png" alt="" v-if="v.type == 0">
                        <span v-if="v.type == 0">{{v.description}}</span>
                        <img src="../../../static/imgs/discount.png" alt="" v-if="v.type == 1">
                        <span v-if="v.type == 1">{{v.description}}</span>
                        <img src="../../../static/imgs/special.png" alt="" v-if="v.type == 2">
                        <span v-if="v.type == 2">{{v.description}}</span>
                        <img src="../../../static/imgs/invoice.png" alt="" v-if="v.type == 3">
                        <span v-if="v.type == 3">{{v.description}}</span>
                        <img src="../../../static/imgs/guarantee.png" alt="" v-if="v.type == 4">
                        <span v-if="v.type == 4">{{v.description}}</span>
                    </div>
                </div>
            </div>
            <div class="empty2"></div>
            <div class="seller-pics">
                <p class="pics-title">商家实景</p>
                <div  class="pics" ref="picWidth">
                    <ul class='pic' ref='pics'>
                        <li class = 'picList' v-for="(v,k) in  seller.pics">
                            <img :src="v" alt="" width='120' height='90'>
                        </li>
                    </ul>
                </div>
            </div>
            <div class="empty3"></div>
            <div class="seller-news">
                <p class="news-title">商家信息</p>
                <div class="news-infos">
                    <div v-for="(v,k) in seller.infos" class="infos">
                        <span>{{v}}</span>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import {mapGetters} from 'vuex'
    import Star from '../star/Star.vue'
    import BScroll from 'better-scroll'

    export default {
        data:function(){
            return {
                status:false,
            }
        },
        computed:{
            ...mapGetters([
                'seller'
            ])
        },
        created(){
            this.$store.dispatch('getSeller');
            this.$nextTick( () => {
                setTimeout( ()=> {
                    if( !this.sellerBS ){
                        this.sellerBS = new BScroll (this.$refs.sellers,{
                            click : true
                        });
                    }else{
                        this.sellerBS.refresh();
                    }
                },0)
            });
            this.$nextTick( () => {
                setTimeout( ()=> {
                    if( !this.picsBS ){
                        this.$refs.pics.style.width=500+'px';
                        this.picsBS = new BScroll (this.$refs.picWidth,{
                            click : true,
                            scrollX: true
                        });
                    }else{
                        this.picsBS.refresh();
                    }
                },0)
            })
        },
        methods:{
            changeStatus(e){
                if(!e._constructed){
                  return;
                }
                this.status = !this.status;
            }
        },
        components:{
            Star 
        }
    }
</script>

<style lang="less">
    .seller{
        width:100%;
        position:fixed;
        top:3.48rem;
        bottom:0.96rem;
        overflow:hidden;
        z-index: -5;
        // 配送信息
        .seller-top{
            // 品牌
            .top-brand{
                width:100%;
                padding:0.36rem 0.36rem 0 0.36rem;
                display:flex;
                .left{
                    // 品牌名称
                    .brand{
                        font-size:0.28rem;
                        color:rgb(7,17,27);
                        line-height:0.28rem;
                    }
                    .left-cont{
                        display:flex;
                        // 星星评分
                        .star{
                            position:relative;
                            top:0.16rem;
                            left:0.1rem;
                            width:0.5rem;
                            & img{
                                width:0.36rem;
                            }
                        }
                        // 文字
                        .top-text1{
                            font-size:0.20rem;
                            color:rgb(77,85,96);
                            line-height:0.36rem;
                            position:relative;
                            left:1.56rem;
                            top:0.20rem;
                        }
                        .top-text2{
                            font-size:0.20rem;
                            color:rgb(77,85,96);
                            line-height:0.36rem;
                            position:relative;
                            left:2.04rem;
                            top:0.20rem;
                        }
                    }
                }
                // 收藏
                .collect{
                    position:relative;
                    left: 3.6rem;
                    // 心
                    & > .like{
                        font-size:0.48rem;
                        line-height:0.48rem;
                        color:red;
                    }
                    &> .unlike{
                            font-size:0.48rem;
                            line-height:0.48rem;
                            color:#ccc;
                    }
                    // 文字
                    .collect-text{
                        position:relative;
                        top: 0.08rem;
                        font-size:0.20rem;
                        color:rgb(77,85,96);
                        line-height:0.20rem;
                    }
                }
            }
            .h{
                border-top:1px solid rgba(7,17,27,0.1);
                margin:0.36rem;
            }
            // 配送细节
            .top-detail{
                width:100%;
                text-align:center;
                display:flex;
                justify-content:space-around;
                .tab-item{
                    flex:3;
                    border-right:1px solid rgba(7,17,27,0.1);
                    .text1{
                        font-size:0.20rem;
                        color:rgb(147,153,159);
                        line-height:0.20rem;
                    }
                    .text2{
                        font-size:0.40rem;
                        font-weight:200;
                        color:rgb(7,17,27);
                        line-height:0.48rem;
                        font-family:"宋体";
                    }
                }
            }
        }
        // 间隔
        .empty1{
                width:100%;
                height:0.36rem;
                margin-top:0.36rem;
                background:rgba(7,17,27,0.1);
        }
        // 公告与活动
        .seller-active{
            margin:0.36rem 0.36rem 0 0.36rem;
            .active-title{
                font-size:0.28rem;
                color:rgb(7,17,27);
                line-height:0.28rem;
            }
            .bulletin{
                margin:0.16rem 0 0.32rem 0.24rem;
                font-size:0.24rem;
                font-weight:200;
                color:rgb(240,20,20);
                line-height:0.48rem;
            }
            .active-info{
                .info{
                    border-top:1px solid rgba(7,17,27,0.1);
                    padding:0.24rem;
                    & > img {
                        width:0.32rem;
                        border:1px solid rgba(7,17,27,0.1);
                    }
                    & > span {
                        font-size:0.24rem;
                        font-weight:200;
                        color:rgb(7,17,27);
                        line-height:0.32rem;
                        margin-left:0.12rem;
                    }
                }
            }
        }
        // 间隔
        .empty2{
                width:100%;
                height:0.36rem;
                background:rgba(7,17,27,0.1);
        }
        // 实景图片
        .seller-pics{
            margin:0.36rem;
            .pics-title{
                font-size:0.28rem;
                color:rgb(7,17,27);
                line-height:0.28rem;
            }
            // 图片
            .pics{
                padding-top:0.24rem;
                width:100%;
                .pic{
                    width:100%;
                    white-space:nowrap;
                    overflow:hidden;
                    .picList{
                        display:inline-block;
                        margin-right:6px;
                    }
                }
                
            }
        }
        // 间隔
        .empty3{
                width:100%;
                height:0.36rem;
                background:rgba(7,17,27,0.1);
        }
        // 商家信息
        .seller-news{
            margin:0.36rem;
            .news-title{
                font-size:0.28rem;
                color:rgb(7,17,27);
                line-height:0.28rem;
            }
            // 信息
            .news-infos{
                .infos{
                    margin-top:0.18rem;
                    border-top:1px solid rgba(7,17,27,0.1);
                    padding:0.32rem 0 0 0;
                    & > span{
                        margin-left:0.24rem;
                        padding-bottom:0.12rem;
                        font-size:0.24rem;
                        color:rgb(7,17,27);
                        font-family:"宋体";
                    }
                }
            }
        }
    }
</style>


