import type from '../types'
import axios from 'axios'

const state = {
   ratings:[] 
}

const getters = {
    ratings(state){
        return state.ratings
    }
}

const actions = {
    getratings({commit,state}){
        axios.get('../../../static/data.json')
        .then( resp => {
            // console.log(resp);
            if( resp.data.errno === 0){
                commit(type.GET_RATINGS,resp.data.ratings);
            }
        })
    }
}

const mutations = {
    [type.GET_RATINGS](state,data){
        state.ratings = data;
    }
}

export default{
    state,
    getters,
    actions,
    mutations
}


