<template>
	<div class="stars">
		<div v-for="(v,k) in 5">
			<img src="../../../static/imgs/star_on.png" v-if="v<=score" >
			<img src="../../../static/imgs/star_half.png" v-if="v>score&&v<score+1" >
			<img src="../../../static/imgs/star_off.png" v-if="v>=score+1" >
		</div>
	</div>
</template>


<script>
	export default{
		props:["score"]
	}
</script>


<style lang="less" scoped>
	.stars{
		display: flex;
		& img{
			width:0.3rem;
		}
	}
</style>


