<template>
    <div class="goods">
        <div class="goods-menu" ref="menus">
            <div>
                <div v-for="(v,k) in goods" class="menu" :key='k' :class="{'current' : k === currentIndex}" @click="selectMenu(k,$event)">
                    <span>{{v.name}}</span>
                </div>
            </div>  
        </div>
        <div class="goods-foods" ref="foods">
            <div>
                <div v-for="(value,k) in goods" :key='k' class="foodList food-list-hook">
                    <div class="foods-title">
                        <span class="titles">{{value.name}}</span>
                    </div>
                    <div v-if="v" v-for="(v,k) in value.foods" :key='k' class="foods-detail">
                        <img :src="v.image" alt="">
                        <div class="detail">
                            <p class="text1" @click="show($event,v)">{{v.name}}</p>
                            <p class="text2">月售{{v.sellCount}}份&nbsp;&nbsp;好评率{{v.rating}}%</p>
                            <div class="priceCount">
                                <p class="text3">￥{{v.price}}&nbsp;&nbsp;<s class="text4" v-if="v.oldPrice > 0">￥{{v.oldPrice}}</s></p>
                                <showcart class="cart" :food="v"></showcart>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <transition enter-active-class='animated zoomIn' leave-active-class='animated zoomOut'>
            <food-detail :value="value" v-if="showFoodDetail"></food-detail>
        </transition>
    </div>
</template>

<script>
    import {mapGetters} from 'vuex'
    import foodDetail from '../detail/foodDetail.vue'
    import showcart from '../detail/shoppingCart.vue'
    import BScroll from 'better-scroll'

    export default {
        data(){
            return {
                listHeight : [],
                scrollY : 0,
                value:{}
            }
        },
        components:{
            'food-detail' : foodDetail,
            showcart,
        },
        created(){
            this.$store.dispatch('getgoods');
            this.$nextTick( () => {
                setTimeout( ()=> {
                    if( !this.foodsBS ){
                        this.foodsBS = new BScroll (this.$refs.foods,{
                            click : true
                        });
                    }else{
                        this.foodsBS.refresh();
                    }
                },0)
            });
            this.$nextTick( () => {
				setTimeout(() => {
					this._initMenu();
					this._calcHeight();
				},100)
			})
        },
        computed:{
            ...mapGetters(['goods','showFoodDetail']),
			 currentIndex(){
		    	for( let i = 0 ; i < this.listHeight.length ; i++ ){
		    		let height1 = this.listHeight[i];
		    		let height2 = this.listHeight[i+1];
		    		if( this.scrollY >= height1 && this.scrollY < height2 ){
		    			return i;
		    		}
		    	}
		    	return 0;
		    },
        },
		methods : {
            show(e,v){
                this.value=v;
                this.$store.dispatch('showFoodDetail');
            },
			selectMenu(key,e){
				if( !e._constructed ){
					return;
				}
            	let foodList = this.$refs.foods.getElementsByClassName('food-list-hook');
				let el = foodList[key];
				this.foodBS.scrollToElement(el,300);

			},
            _calcHeight(){
            	let foodList = this.$refs.foods.getElementsByClassName('food-list-hook');
            	let height = 0;
            	this.listHeight.push(height);
            	for(let i = 0 ; i < foodList.length ; i++){
            		height += foodList[i].clientHeight;
            		this.listHeight.push(height);
            	}
            },
			_initMenu(){
				this.menuBS  = new BScroll(this.$refs.menus,{click:true});
				this.foodBS = new BScroll(this.$refs.foods,{
					probeType : 3,
				});
				this.foodBS.on('scroll',(pos) =>{
					this.scrollY = Math.abs(Math.round(pos.y));
				})
			}
		},
    }
</script>

<style lang="less" scoped>

    .goods{
        width:100%;
        position:absolute;
        top:3.48rem;
        bottom:0.92rem;
        display:flex;
        z-index: 0;
        // 菜单栏
        .goods-menu{
            flex:0 0 1.60rem;
            width:1.60rem;
            background:#f3f5f7;
            text-align:center;
            overflow:hidden;
            .menu{
                height:1.08rem;
                border-bottom:1px solid rgba(7,17,27,0.1);
                font-size:0.24rem;
                color:rgb(77,85,93);
                font-wieght:200;
                display:table;
                position:relative;
                left:0.24rem;
                // 左右联动样式
                &.current{
					background: white;
				}
                // 文字
                & > span {
                    width:1.12rem;
                    vertical-align:middle;
                    display:table-cell;
                }
            }
        }
        // 食物栏
        .goods-foods{
            flex:1;
            overflow:hidden;
            .foodList{
                // 食物类名
                .foods-title{
                    width:100%;
                    height:0.52rem;
                    display:table;
                    background:#f3f5f7;
                    border-left:4px solid #d9dde1;
                    .titles{
                        vertical-align:middle;
                        display:table-cell;
                        font-size:0.24rem;
                        color:rgb(147,153,159);
                        line-height:0.52rem;
                        position:relative;
                        left:0.28rem;
                    }
                }
                // 同类食物栏
                .foods-detail{
                    display:flex;
                    margin:0 0 0 0.32rem;
                    border-bottom:1px solid rgba(7,17,27,0.1);
                    padding:0.32rem 0 0.32rem 0;
                    // 食物图片
                    img {
                        width:1.28rem;
                        height:1.28rem;
                        position:relative;
                    }
                    // 食物描述
                    .detail{
                        position:relative;
                        left:0.20rem;
                        .text1{
                            font-size:0.28rem;
                            color:rgb(7,17,27);
                            line-height:0.28rem;
                        }
                        .text2{
                            position:relative;
                            top:0.32rem;
                            font-size:0.20rem;
                            color:rgb(147,153,159);
                            line-height:0.20rem;
                        }
                        // 价格
                        .priceCount{
                            display:flex;
                            position:relative;
                            justify-content:space-between;
                            top:0.48rem;
                            width:3.5rem;
                            .text3{
                                font-size:0.28rem;
                                color:rgb(240,20,20);
                                font-weight:700;
                                line-height:0.48rem;
                            }
                            .text4{
                                font-size:0.20rem;
                                color:rgb(147,153,159);
                                font-weight:700;
                                line-height:0.48rem;
                            }
                            .cart{
                                padding-left:0.4rem;
                            }
                        }
                    }
                }
            }
        }
    }
</style>


