<template>
    <div class="ratings">
        <div class="score">
            <div class="left">
                <p class="text1">{{seller.foodScore}}</p>
                <p class="text2">综合评分</p>
                <p class="text3">高于周边商家{{seller.rankRate}}%</p>
            </div>
            <div class="right">
                <div class="foodScore">
                    <span class="text4">商品得分</span>
                    <div class="star1">
                        <Star :score='seller.foodScore'></Star>
                    </div>
                    <span class="text6">{{seller.foodScore}}</span>
                </div>
                <div class="serviceScore">
                    <span class="text7">服务态度</span>
                    <div class="star2">
                        <Star :score='seller.serviceScore'></Star>
                    </div>
                    <span class="text9">{{seller.serviceScore}}</span>
                </div>
                <div class="deliveryTime">
                    <span class="text10">送达时间</span>
                    <span class="text11">{{seller.deliveryTime}}分钟</span>
                </div>
            </div>
        </div>
        <div class="empty"></div>
        <div class="rating" ref="Rating">
            <div class="ratingCount">
                <button class="all" @click="getAll"><span>全部{{ratings.length}}</span></button>
                <button class="satisfy" @click="getSatisfy"><span>满意{{num}}</span></button>
                <button class="unsatisfy" @click="getUnsatisfy"><span>不满意{{ratings.length-num}}</span></button>
            </div>
        </div>
        <div class="show" @click="status=!status">
                <i class="icon iconfont" :class="status?'on':'off'">&#xe608;</i>
                <span>只看有内容的评价</span>
        </div>
        <div class="cont" ref="Rating">
            <div>
                <ul>
                    <li v-for="(v,k) in ratings" :key='k' v-show="all || yes === v.rateType" v-if="v.text||status">
                        <div class="contTop">
                            <div class="contLeft">
                                <img :src="v.avatar" alt="">
                                <span class="name">{{v.username}}</span>
                                <div class="cont1">
                                    <Star :score='v.score' class="star3"></Star>
                                    <span class="time">{{v.deliveryTime}}分钟送达</span>
                                </div>
                            </div>
                            <div class="contRight">
                                <p>{{rateTime[k]}}</p> 
                            </div>
                        </div>
                        <div class="contBottom">
                            <p class="text">{{v.text}}</p>
                            <div class="cont2">
                                <i v-if="v.rateType == 0" class="iconfont icon-damuzhion"></i>
                                <i v-else="v.rateType == 1" class="iconfont icon-down"></i>
                                <div class="recommend">
                                    <span v-for="(value,k) in v.recommend" v-if="k<3">{{value}}</span>
                                </div>
                            </div>
                        </div>
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
    import {mapGetters} from 'vuex'
    import BScroll from 'better-scroll'
    import Star from '../star/newStar.vue'

    export default {
        data(){
            return {
                all: true,
                yes: -1 ,
                status:true,
            }
        },
        computed:{
            ...mapGetters([
                'ratings',
                'seller'
            ]),
            rateTime(){
                var rateTime_arr=[];
                for(var i in this.ratings){
                    var rateTime=this.ratings[i].rateTime;
                    var time= new Date(rateTime);
                    var y = time.getFullYear();//年
                    var m = time.getMonth() + 1;//月
                    var d = time.getDate();//日
                    var h = time.getHours();//时
                    var mm = time.getMinutes();//分
                    var s = time.getSeconds();//秒
                    rateTime_arr.push(y+"-"+m+"-"+d+" "+h+":"+mm+":"+s)
                }
                // console.log(rateTime_arr);
                return rateTime_arr
            },
            num(){
                return this.ratings.filter((item) => {
                    return item.rateType === 0
                }).length
            }
        },
        created(){
            this.$store.dispatch('getratings');
            this.$store.dispatch('getSeller');
            this.$nextTick( () => {
                setTimeout( ()=> {
                    if( !this.RatingBS ){
                        this.RatingBS = new BScroll (this.$refs.Rating,{
                            click : true
                        });
                    }else{
                        this.RatingBS.refresh();
                    }
                },0)
            });
        },
        components:{
            Star 
        },
        methods:{
            getSatisfy() {
                this.yes = 0
                this.all = false
            },
            getUnsatisfy() {
                this.yes = 1
                this.all = false
            },
            getAll() {
                this.yes = -1
                this.all = true
            }
        }
    }
</script>

<style lang="less" scoped>
    .ratings{
        width:100%;
        position:fixed;
        top:3.48rem;
        bottom:0.92rem;
        overflow:hidden;
        z-index: -5;
        // 评分
        .score{
            width:100%;
            padding:0.36rem 0 0.36rem 0;
            display:flex;
            // 综合评分
            .left{
                width:2.75rem;
                border-right:1px solid rgba(7,17,27,0.1);
                .text1{
                    font-size:0.48rem;
                    color:rgb(255,153,0);
                    line-height:0.56rem;
                    text-align:center;
                    padding:0.12rem 0 0.12rem 0;
                }
                .text2{
                    font-size:0.24rem;
                    color:rgb(7,17,27);
                    line-height:0.24rem;
                    text-align:center;
                    padding:0 0 0.12rem 0.16rem;
                }
                .text3{
                    font-size:0.20rem;
                    color:rgb(7,17,27);
                    line-height:0.20rem;
                    text-align:center;
                    padding:0 0 0 0.12rem;
                }
            }
            // 分类评分
            .right{
                width:4.74rem;
                padding:0 0 0 0.48rem;
                // 食物评分
                .foodScore{
                    width:100%;
                    .text4{
                        font-size:0.24rem;
                        color:rgb(7,17,27);
                        line-height:0.36rem;
                    }
                    .star1{
                        position:relative;
                        left:1.14rem;
                        top:-0.35rem;
                        width:1.6rem;
                        height:0.03rem;
                    }
                    .text6{
                        font-size:0.24rem;
                        color:rgb(255,153,0);
                        line-height:0.36rem;
                        position:relative;
                        left:2.98rem;
                        bottom:0.4rem;;
                    }
                }
                // 态度评分
                .serviceScore{
                    width:100%;
                    .text7{
                        font-size:0.24rem;
                        color:rgb(7,17,27);
                        line-height:0.36rem;
                    }
                    .star2{
                        position:relative;
                        left:1.14rem;
                        top:-0.35rem;
                        width:1.6rem;
                        height:0.03rem;
                    }
                    .text9{
                        font-size:0.24rem;
                        color:rgb(255,153,0);
                        line-height:0.36rem;
                        position:relative;
                        left:2.98rem;
                        bottom:0.4rem;
                    }
                }
                // 配送时间
                .deliveryTime{
                    width:100%;
                    .text10{
                        font-size:0.24rem;
                        color:rgb(7,17,27);
                        line-height:0.36rem;
                    }
                    .text11{
                        font-size:0.24rem;
                        color:rgb(147,153,159);
                        line-height:0.36rem;
                        padding:0 0.24rem 0 0.24rem;
                    }
                }
            }
        }
        // 间隔
        .empty{
            width:100%;
            height:0.36rem;
            background:rgba(7,17,27,0.1);
        }
        // 评价
        .rating{
            margin:0 0.36rem 0 0.36rem;
            border-bottom:1px solid rgba(7,17,27,0.1);
            .ratingCount{
                margin:0.36rem 0 0.36rem 0;
                // 评价按钮
                & > button {
                    padding:0.16rem 0.24rem;
                    margin-right:0.16rem;
                    border:0;
                    }
                .all{
                        border-radius:0.02rem;
                        background-color:rgb(0,160,220);
                        & > span {
                            font-size:0.24rem;
                            color:rgb(255,255,255);
                            line-height:0.32rem;
                        }
                    }
                    .satisfy{
                        border-radius:0.02rem;
                        background-color:skyblue;
                        & > span {
                            font-size:0.24rem;
                            color:rgb(77,85,93);
                            line-height:0.32rem;
                        }
                    }
                    .unsatisfy{
                        border-radius:0.02rem;
                        background-color:rgba(7,17,27,0.1);
                        & > span {
                            font-size:0.24rem;
                            color:rgb(77,85,93);
                            line-height:0.32rem;
                        }
                }
            }
        }
        // 只看有内容的评价
        .show{
            padding:0.24rem 0.36rem;
            border-bottom:2px solid rgba(7,17,27,0.1);
            .on{
                font-size:0.48rem;
                color:rgb(147,153,159);
                line-height:0.48rem;
            }
            .off{
               font-size:0.48rem;
                color:rgb(0, 160, 220);
                line-height:0.48rem;
            }
            & > span {
                margin-left:0.08rem;
                font-size:0.24rem;
                color:rgb(147,153,159);
                line-height:0.48rem;
            }
        }
        // 评价
        .cont{
            overflow:hidden;
            height:3.8rem;
            li{
                margin:0 0.36rem 0 0.36rem;
                overflow:hidden;
                padding:0.36rem;
                border-bottom:1px solid rgba(7,17,27,0.1);
                .contTop{
                    display:flex;
                    .contLeft{
                        // 头像
                        & img{
                            border-radius:50%;
                            width:0.56rem;
                        }
                        // 昵称
                        .name{
                            position:relative;
                            left:0.24rem;
                            bottom:0.36rem;
                            font-size:0.20rem;
                            color:rgb(7,17,27);
                            line-height:0.24rem;    
                        }
                        .cont1{
                            display:flex;
                            // 星星评分
                            .star3{
                                position:relative;
                                left:0.8rem;
                                bottom:0.32rem;
                            }
                            // 配送时间
                            .time{
                                color: rgb(147, 153, 159);
                                font-size: 0.26rem;
                                font-weight:200;
                                line-height: 0.24rem;
                                position:relative;
                                left:0.92rem;
                                bottom:0.28rem;
                            }
                        }
                    }
                    .contRight{
                        // 时间
                        & > p{
                            position:relative;
                            left:2.0rem;
                            color: rgb(147, 153, 159);
                            font-size: 0.20rem;
                            font-weight:200;
                            line-height: 0.24rem;
                        }
                    }
                }
                .contBottom{
                    // 评价文本
                    & > p {
                        position:relative;
                        left:0.88rem;
                        bottom:0.16rem;
                        color: rgb(7,17,27);
                        font-size: 0.24rem;
                        line-height: 0.36rem;
                    }
                    .cont2{
                        dispaly:flex;
                        position:relative;
                        left:0.88rem;
                        // 点赞
                        .icon-damuzhion{
                            color: rgb(0, 160, 220);
                            font-size: 0.24rem;
                            line-height: 0.32rem;
                        }
                        // 倒彩
                        .icon-down{
                            color: rgb(147, 153, 159);
                            font-size: 0.24rem;
                            line-height: 0.32rem;
                        }
                        // 推荐菜
                        .recommend{
                            dispaly:flex;
                            position:relative;
                            left:0.16rem;
                            bottom:0.3rem;
                            & > span {
                                margin:0.16rem;
                                padding:0.12rem;
                                color: rgb(147, 153, 159);
                                font-size: 0.18rem;
                                line-height: 0.32rem;
                                border:1px solid rgba(7,17,27,0.1);
                                border-radius:0.02rem;
                                background-color:rgb(255,255,255);
                            }
                        }
                    }
                }
            }   
        }
    }
</style>


