import Vue from 'vue'
import Vuex from 'vuex'
import actions from './actions'
import goods from './modules/goods'
import ratings from './modules/ratings'
import seller from './modules/seller'

Vue.use(Vuex);

export default new Vuex.Store({
    actions,
    modules:{
        goods,
        ratings,
        seller
    }
});


