<template>
    <transition>
        <div class="detail" v-show = 'showdetail'>
            <div class="detail-Top">   
                <h1 class="title">{{seller.name}}</h1>
                <Star :score = 'seller.score'></Star>
            </div>
            <div class="detail-center">
                <div class="info">
                    <div class="info-top">
                        <div class="info-line1"></div>
                        <div class="info-title">
                            优惠信息
                        </div>
                        <div class="info-line2"></div>
                    </div>
                    <div class="info-bottom">
                        <div v-for="(v,k) in seller.supports">
                            <img src="../../../static/imgs/decrease.png" alt="" v-if="v.type == 0">
                            <p v-if="v.type == 0">{{v.description}}</p>
                            <img src="../../../static/imgs/discount.png" alt="" v-if="v.type == 1">
                            <p v-if="v.type == 1">{{v.description}}</p>
                            <img src="../../../static/imgs/special.png" alt="" v-if="v.type == 2">
                            <p v-if="v.type == 2">{{v.description}}</p>
                            <img src="../../../static/imgs/invoice.png" alt="" v-if="v.type == 3">
                            <p v-if="v.type == 3">{{v.description}}</p>
                            <img src="../../../static/imgs/guarantee.png" alt="" v-if="v.type == 4">
                            <p v-if="v.type == 4">{{v.description}}</p>
                        </div>
                    </div>
                </div>
                <div class="public">
                    <div class="public-top">
                        <div class="public-line1"></div>
                        <div class="public-title">
                            商家公告
                        </div>
                        <div class="public-line2"></div>
                    </div>
                    <div class="public-bottom">
                        {{seller.bulletin}}
                    </div>
                </div>
            </div>
            <div class="detail-bottom">
                <i class="icon iconfont" @click="closeDetail">&#xe648;</i>
            </div>
        </div> 
    </transition>
</template>

<script>
    import {mapGetters} from 'vuex'
    import Star from '../star/Star.vue'

    export default {
        computed:{
            ...mapGetters([
                'seller',
                'showdetail'
            ])
        },
        created(){
            this.$store.dispatch('getSeller');
        },
        components:{
            Star 
        },
        methods:{
            closeDetail(){
                this.$store.dispatch('closedetail')
            }
        }
    }
</script>

<style lang="less" scoped>
    .detail{
        position:fixed;
        top:0;
        left:0;
        width:100%;
        height:100%;
        background-color:rgba(7,17,27,0.8);
        z-index:999;
        // 品牌名
        .detail-Top{
            .title{
                font-size: 0.32rem;
                color: rgb(255,255,255);
                font-weight: 700;
                line-height: 0.32rem;
                text-align:center;
                position:relative;
                top:1.28rem;
            }
        }
        .detail-center{
            // 优惠信息
            .info{
                position:relative;
                top:2.80rem;
                .info-top{
                    // 线条
                    .info-line1{
                        position:absolute;
                        left:0.72rem;
                        top:0.15rem;
                        width:2.24rem;
                        border-bottom:2px solid rgba(255,255,255,0.2);
                    }
                    // 标题
                    .info-title{
                        font-size: 0.32rem;
                        color: rgb(255,255,255);
                        font-weight: 700;
                        line-height: 0.32rem;
                        text-align:center;
                    }
                    // 线条
                    .info-line2{
                        position:absolute;
                        right:0.72rem;
                        top:0.15rem;
                        width:2.24rem;
                        border-bottom:2px solid rgba(255,255,255,0.2);
                    }
                }
                // 优惠文字
                .info-bottom{
                    position:relative;
                    left:0.96rem;
                    top:0.49rem;
                    img{
                        height:0.32rem;
                    }
                    p{
                        font-size:0.24rem;
                        font-wight:200;
                        color:rgb(255,255,255);
                        line-height:0.24rem;
                        font-family:"宋体";
                        position:relative;
                        right:0.24rem;
                        left:0.5rem;
                        bottom:0.31rem;
                    }
                }
            }
            // 商家公告
            .public{
                position:relative;
                top:3.60rem;
                .public-top{
                    // 线条
                    .public-line1{
                        position:absolute;
                        left:0.72rem;
                        top:0.15rem;
                        width:2.24rem;
                        border-bottom:2px solid rgba(255,255,255,0.2);
                    }
                    // 标题
                    .public-title{
                        font-size: 0.32rem;
                        color: rgb(255,255,255);
                        font-weight: 700;
                        line-height: 0.32rem;
                        text-align:center;
                    }
                    // 线条
                    .public-line2{
                        position:absolute;
                        right:0.72rem;
                        top:0.15rem;
                        width:2.24rem;
                        border-bottom:2px solid rgba(255,255,255,0.2);
                    }
                }
                // 公告文字
                .public-bottom{
                    width:5.60rem;
                    position:absolute;
                    top:0.64rem;
                    left:1.00rem;
                    font-size: 0.24rem;
                    color: rgb(255,255,255);
                    font-weight: 200;
                    line-height: 0.48rem;
                    font-family:"宋体";
                }
            }
        }
        // 关闭图标
        .detail-bottom{
            position:relative;
            bottom: -7.5rem;
            text-align:center;
            .iconfont{
                font-size: 0.48rem;
                color: rgba(255,255,255,0.5);
            }
        }
    }
</style>


