<template>
    <div class="star">
        <div v-for="(v,k) in 5">
            <img src="../../../static/imgs/star_on.png" alt="" v-if="star_arr[k] == 1">
            <img src="../../../static/imgs/star_half.png" alt="" v-if="star_arr[k] == 0.5">
            <img src="../../../static/imgs/star_off.png" alt="" v-if="star_arr[k] == 0">
        </div>
    </div>
</template>

<script>
    import {mapGetters} from 'vuex'
    
    export default {
        computed:{
            ...mapGetters([
                'star_arr'
            ])
        }
    }
</script>

<style lang="less" scoped>
    .star{
        width:4.00rem;
        display:flex;
        position:absolute;
        left:1.80rem;
        top:2.00rem;
        justify-content:space-between;
    }
</style>


