import type from '../types'
import axios from 'axios'

const state = {
    seller:[],
    showdetail : false,
    star_arr:[]
}

const getters = {
    seller(state){
        return state.seller;
    },
    showdetail(state){
        return state.showdetail;
    },
    star_arr(state){
        return state.star_arr;
    }
}

const actions = {
    getSeller({commit,state}){
        axios.get('../../../static/data.json')
        .then( resp => {
            // console.log(resp);
            // console.log(resp.data.data.score);
            if( resp.data.errno === 0 ){
                commit(type.GET_SELLER,resp.data.seller);
                commit(type.GET_STAR,resp.data.seller.score);
            }
        })
    },
    showDetail({commit}){
        commit(type.SHOW_DETAIL);
    },
    closedetail({commit}){
        commit(type.HIDE_DETAIL);
    }
}
const mutations = {
    [type.GET_SELLER](state,data){
        state.seller = data;
    },
    [type.SHOW_DETAIL](state){
        state.showdetail = true;
    },
    [type.HIDE_DETAIL](state){
        state.showdetail = false;
    },
    [type.GET_STAR](state,score){
        var on = parseInt(score);
        var half = score%1;
        var off = Math.ceil(5-score); //向上取整
        var star_arr = [];
        for(var i=0; i<on; i++){
            star_arr.push(1); 
        }
        if(half != 0){
            star_arr.push(0.5);
            off--;
        }
        for(var i=0; i<off; i++){
            star_arr.push(0);
        }
        state.star_arr = star_arr;
    }
}

export default{
    state,
    getters,
    actions,
    mutations
}


