<template>
    <div class="list">
        <div class="content">
            <div class="top">
                <span class="text1">购物车</span>
                <span class="text2" @click="clear">清空</span>
            </div>
            <div class="bottom" ref="listWrap">
                <div>
                    <ul>
                        <li v-for="(v,k) in food">
                            <div class="left">
                                <span>{{v.name}}</span>
                            </div>
                            <div class="right">
                                <span>￥{{v.price}}</span>
                                <showcart class="cart" :food="v"></showcart>
                            </div>
                        </li>
                    </ul>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import Vue from 'vue'
    import {mapGetters} from 'vuex'
    import BScroll from 'better-scroll'
    import showcart from '../detail/shoppingCart.vue'
    
    export default {
        data(){
            return {
                show:true
            }
        },
        props:['food'],
        components:{
            showcart
        },
        computed:{
            ...mapGetters([
                'showList'
            ])
        },
        created(){
            this.$nextTick( () => {
                setTimeout( ()=> {
                    if( !this.listWrapBS ){
                        this.listWrapBS = new BScroll (this.$refs.listWrap,{
                            click : true
                        });
                    }else{
                        this.listWrapBS.refresh();
                    }
                },0)
            });
        },
        methods:{
            showCount(){
                Vue.set(this.value,'count',1);
            },
            clear() {
                    // this.$store.dispatch('getgoods')
                    this.food.forEach((value)=>{
                        this.$delete(value,"count");
                    });
                    this.$store.dispatch('showFoodList',false)
            }
        }
    }
</script>

<style lang="less" scoped>
    .slide-fade-enter-active {
        transition: all .3s ease;
    }
    .slide-fade-leave-active {
        transition: all .8s cubic-bezier(1.0, 0.5, 0.8, 1.0);
    }
    .slide-fade-enter, .slide-fade-leave-to
        /* .slide-fade-leave-active for below version 2.1.8 */ {
        transform: translateX(10px);
        opacity: 0;
    }
    .list{
        width:100%;
        position: fixed;
        bottom:0.92rem;
        left:0;
        z-index: -55;
        .content{
            max-height:5.19rem;
            background:white;
            .top{
                display:flex;
                justify-content:space-between;
                padding:0 0.36rem;
                height:0.8rem;
                background:#f3f5f7;
                border-bottom:1px solid rgba(7,17,27,0.1);
                // 购物车
                .text1{
                    font-size:0.28rem;
                    font-weight:200;
                    color:rgb(7,17,27);
                    line-height:0.8rem;
                }
                // 清空
                .text2{
                    font-size:0.24rem;
                    color:rgb(0,160,220);
                    line-height:0.8rem;
                }
            }
            .bottom{
                margin:0 0.36rem;
                max-height:4.39rem;
                overflow:hidden;
                & li{
                    border-bottom:1px solid rgba(7,17,27,0.1);
                    display:flex;
                    height:0.48rem;
                    padding:0.24rem 0;
                    justify-content:space-between;
                    .left{
                        & > span {
                            font-size:0.28rem;
                            color:rgb(7,17,27);
                            line-height:0.48rem;
                        }
                    }
                    .right{
                        display:flex;
                        & > span {
                            font-size:0.28rem;
                            font-weight:700;
                            color:rgb(240,20,20);
                            line-height:0.48rem;
                            margin:0 0.24rem 0 0.36rem;
                        }
                        .cart{
                            font-size:0.28rem;
                            color:rgb(7,17,27);
                            line-height:0.48rem;
                        }
                    }
                }
            }
        }
    }
</style>


